import React, { useEffect, useState } from 'react'
import { Button, Card, Col, Form, Row } from 'react-bootstrap'
import {app} from '../firebase'
import {getFirestore, doc, setDoc, getDoc} from 'firebase/firestore'
import {getStorage, uploadBytes,ref, getDownloadURL} from 'firebase/storage'
import { async } from '@firebase/util'

const MyPage = () => {
    const [loading, setLoading] = useState(false);
    const db = getFirestore(app);
    const storage = getStorage(app);
    const [form, setForm] = useState({
        email:sessionStorage.getItem('email'),
        name:'',
        address:'',
        photo:''
    });
    const {email,name,address,photo} = form;
    const [image, setImage] = useState('');
    const [file, setFile] = useState(null);
    const onChangeFile = (e) => {
        setImage(URL.createObjectURL(e.target.files[0]));
        setFile(e.target.files[0])
    }
    const onChange = (e) => {
        setForm({...form, [e.target.name]:e.target.value});
    }
    const onSubmit = async(e) => {
        e.preventDefault();
        if(!window.confirm('?')) return;
        setLoading(true);
        //
        let url="";
        if(file !== null){//  
            const fileName=`images/${Date.now()}_${file.name}`
            const result = await uploadBytes(ref(storage, fileName),file);
            url = await getDownloadURL(result.ref);
        }
        //
        await setDoc(doc(db,'users',email),{...form, photo:url})
        setLoading(false);
    }
    const getInfo = async() => {
        setLoading(true);
        const result=await getDoc(doc(db,'users', email));
        setForm(result.data());
        setImage(result.data().photo)
        setLoading(false);
    }

    useEffect(()=>{
        getInfo();
    },[])
    if(loading) return <h1>Loading...</h1>
  return (
    <Row className='justify-content-center my-5'>
        <Col md={6}>
            <Card>
                <Card.Title className='p-3 text-center'>
                    <h2></h2>
                </Card.Title>
                <Card.Body>
                    <Form className='text-center' onSubmit={onSubmit}>
                        <Form.Control
                        name="name" value={name} onChange={onChange}
                        placeholder='' className='my-3'/>
                        <Form.Control
                        name='address' value={address} onChange={onChange}
                        placeholder='' className='my-3'/>
                        <img src={image ? image:"http://via.placeholder.com/100x120"} style={{width:'20%'}}/>
                        <Form.Control
                            type="file" placeholder='' className='my-3'
                            onChange={onChangeFile}
                        />
                        <Button type='submit' className='mt-1'></Button>
                    </Form>
                </Card.Body>
            </Card>
        </Col>
    </Row>
  )
}

export default MyPage